body {
  background-color: #222629;
  color: white;
  margin: 0;
  font-family: "Roboto", sans-serif;
}

h1 {
  padding-left: 1.2em;
  padding-top: 1em;
  padding-bottom: .5em;
  font-family: "Arial", sans-serif;
  color: #86C232;
  background-color: #333;
  margin-top: 0;
  overflow: hidden;
}



ul {
  list-style: none;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
}

ul li {
  margin-bottom: 8px;
}

.fa-edit {
  margin-left: 8px;
  font-size: 10px;
  vertical-align: middle;
}

#addItem {
  margin-left: 0;
  text-align: left;
  font-size: 14px;
  color: #6B6E70;
}


.taskList {
  list-style: none;
}


button {
  margin-left: 3em;
  background-color: darkred;
  box-shadow: black;
  font-family: "Roboto", sans-serif;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

#uncomplete {
  margin: 3em 1em 3em 3.1em;
  font-size: 14px;
}
.fa-info-circle {
  color: #86C232;
}

input[type=text] {
  font-family: "Roboto", sans-serif;
  color: white;
  background-color: transparent;
  margin-left: 7px;
  margin-bottom: .5px;
  outline: 0;
  border: 0;
  border-bottom: .25px solid #86C232;
}

input[class=listItemValue] {
  border-bottom: none;
}

input[class=greenCheck] {
  display: inline-block;
  position: relative;
  background-color: #86C232;
}

.greenCheck {
  background: #86C232;
}

input[class=toggle] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-tap-highlight-color: transparent;
  cursor: pointer;
}

input[type=checkbox] {
  vertical-align: middle;
}

input[type=checkbox]:focus {
  outline: 0;
}

.toggle {
  height: 16px;
  width: 37px;
  border-radius: 16px;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  margin: 0;
  border: 2px solid #474755;
  background: linear-gradient(180deg, #2D2F39 0%, #1F2027 100%);
  transition: all 0.2s ease;
}
.toggle:after {
  content: "";
  position: absolute;
  top: -.5px;
  left: .5px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: white;
  box-shadow: 0 1px 2px rgba(44, 44, 44, 0.2);
  transition: all 0.2s cubic-bezier(0.5, 0.1, 0.75, 1.35);
}

.toggle:checked {
  border-color: #86C232;
}
.toggle:checked:after {
  transform: translatex(20px);
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 300pt;
  right: 300pt;
  width: 360px;
  height: 640px;
  background-color: rgba(0, 0, 0, .8);
}

.modal {
  background-color: #222629;
  border-radius: 5pt;
  border-color: black;
  border-style: solid;
  margin: 0 auto;
  top: 50pt;
  height: 100pt;
  width: 200pt;
  padding: 10pt;
  position: relative;
  text-align: center;
  justify-content: center;
}

.alert-buttons {
  margin: 0 auto;
  position: absolute;
  display: flex;
  bottom: 15pt;
  left: 0;
}


.alert-cancel {
  background-color: #86C232;
}